import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import '../src/css/output.css?ver=3';
import 'fullpage.js/vendors/scrolloverflow'; // Optional. When using scrollOverflow:true
import ReactFullpage from '@fullpage/react-fullpage';
import App from './components/App/App.jsx';
import HeaderBlock from './components/HeaderBlock/HeaderBlock.jsx';
import header_man1 from './images/header/man1.png';
import header_man2 from './images/header/man2.png';
import video_bg from './video/bg.mp4';

const fullpageOptions = {
  anchors: ['firstPage', 'secondPage', 'thirdPage'],
  sectionsColor: ['#282c34', '#ff5f45', '#0798ec'],
  callbacks: ['onLeave'],
  scrollOverflow: true,
};

const FullpageWrapper = fullpageProps => (
  <ReactFullpage
    {...fullpageProps}
    render={({ state, fullpageApi }) => {
      console.log('render prop change', state); // eslint-disable-line no-console

      return (
        <div id="fullpage-wrapper">
          <div className="section section1">
            <h3>Section 1</h3>
            <button onClick={() => fullpageApi.moveSectionDown()}>
              Move down
            </button>
          </div>
          <div className="section">
            <div className="slide">
              <h3>Slide 2.1</h3>
            </div>
            <div className="slide">
              <h3>Slide 2.2</h3>
            </div>
            <div className="slide">
              <h3>Slide 2.3</h3>
            </div>
          </div>
          <div className="section">
            <h3>Section 3</h3>
          </div>
        </div>
      );
    }}
  />
);

        ReactDOM.render( <FullpageWrapper {...fullpageOptions} /> , document.getElementById('root'));


